  width: 400px;
}

.currently-playing-container span {
  display: -ms-flexbox;
  display: flex;
  padding: 17px 15px;
}

.currently-playing-container img {
  height: 56px;
  margin-right: 15px;
  width: 56px;
}

.currently-playing-info {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
