.Controls {
  -ms-flex-align: center;
      align-items: center;
  color: #B0AFAF;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-size: 1.2rem;
  padding: 10px;
}

.Controls-buttons button {
  background: none;
  border: none;
  margin: 3px;
}

.Controls-buttons button:hover {
  cursor: pointer;
}

.Controls-buttons svg {
  color: #B0AFAF;
  height: 15px;
  width: 15px;
}

.Controls-buttons svg:hover {
  color: #FFF;
}

.Controls-buttons .play-pause svg {
  height: 40px;
  width: 40px;
}

.Controls-progress {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.progress-time,
.progress-time-total {
  width: 26px;
}

.progress-bar {
  background-color: #4B4B4B;
  border-radius: 8px;
  height: 4px;
  margin: 8px;
  width: 400px;
}

.progress-bar-completed {
  background-color: #B0AFAF;
  border-radius: 8px;
  height: 4px;
}



