.App-main {
  -ms-flex-positive: 1;
      flex-grow: 1;
  overflow-y: auto;
}

.App-main h2 {
  text-align: center;
}

.Browse {
  color: #FFF;
  width: 80%;
  margin: 0 auto;
}

.Browse h2 {
  font-size: 3.6rem;
  margin-bottom: 0;
}

.category-nav ul {
  font-size: 1.4rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  letter-spacing: 0.08rem;
}

.category-nav li {
  font-weight: 500;
  padding: 10px;
  margin: 5px;
  text-transform: uppercase;
}

.category-nav a {
  color: #B0AFAF;
  padding-bottom: 5px;
  text-decoration: none;
}

.category-nav a:hover {
  color: #FFF;
}

.category-nav a.selectedCategory {
  border-bottom: 2px solid #2EB925;
  color: #FFF;
}

.tilelist {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  list-style: none;
  margin: 0;
  padding: 0;

  display: grid;
  grid-template-columns: repeat(6, auto);
  grid-template-rows: auto;
}

.tile {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  line-height: 1.8rem;
  margin: 20px 8px;
  position: relative;
  text-align: center;
  width: 150px;
}

.thumbnail {
  margin-bottom: 10px;
}

.thumbnail img {
  -webkit-box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.25);
          box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.25);
  height: 150px;
}

.overlay {
  background-color: rgba(0, 0, 0, 0.7);
  display: none;
  height: 150px;
  position: absolute;
  top: 0;
  width: 150px;
}

.thumbnail:hover .overlay {
  cursor: pointer;
  display: block;
}

.tile .title:hover {
  cursor: pointer;
}

.albumName {
  font-weight: 500;
  line-height: 1.5;
}

.artistName {
  color: #B0AFAF;
  line-height: 1.5;
}

.artistName:hover {
  color: #FFF;
  cursor: pointer;
  text-decoration: underline;
  -webkit-text-decoration-skip: ink;
          text-decoration-skip: ink;
}



